html {
    background-color: #c5c5c5;
}

body {
    position: relative;
    background-color: red;
    /* margin: 0; */
}

#body-content {
    display: flex;
    flex-direction: column;
}

#upper-side {
    height: 10vh;
    max-width: 100vw;
    background-color: #c02a5c;
    color: whitesmoke;
    display: flex;
    flex-direction: row;
}

#pp-container {
    margin-left: 1em;
    padding-top: 10%;
    position: relative;
}

#img-pp {
    aspect-ratio: 1/1;
    margin-left: 1em;
}

#personal-info {
    text-align: start;
    margin: auto;
    line-height: 2em;
    border-width: 2px;
    width: 300px;
    margin-left: 1em;
}

#upper-right-side {
    display: flex;
    height: auto;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    margin-right: 10%;
}

.top-right-element {
    margin-left: 4em;
}

#exam-title {
    position: absolute;
    /* align-self: center;*/
    color: whitesmoke;
    margin-top: 10px;
    left: 50%;
    transform: translate(-50%, 0%);
}

#main-body-content {
    display: flex;
    flex-direction: row;
    background-color: lightgrey;
    height: auto;
    width: 100%;
}

#answer-side {
    display: flex;
    flex-direction: column;
    /* justify-content: space-evenly; */
    width: 50vw;
    min-height: auto;
    padding: 20px 20px 20px 20px;
    background-color: lightgrey;
}

.img-answer {
    /* width: 100px;
    height: auto;
    margin: 5px;
    */
    width: 0;
    height: 0;
    visibility: hidden;  
}

.answer-format {
    width: 100%;
    height: 25%;
    border: 2px solid #fff;
    border-left: 4px solid #fff;
    border-right: 4px solid #fff;
    border-radius: 10px;
    text-align: center;
}

.text-label {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    color: #c02a5c;
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

label:before {
    position: absolute;
    content: "";
    border-radius: 50%;
    margin-right: 20px;
}

input[type="radio"]:checked + label{
    background-color: #c02a5c;
    color: white;
}

input[type="radio"]{
    display: none;
} 

#question-side {
    width: 50vw;
    min-height: auto;
    padding: 0px 20px 0px 20px;
    background-color: whitesmoke;
    display: flex;
    flex-direction: column;
}

#question-img {
    width: 50%;
    height: auto;
    align-self: center;
}

#question-side-header {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    /* background-color: saddlebrown; */
    margin-bottom: -15px;
}

#progress-bar-div {
    width: 20%;
}

#progress-bar-div progress {
    margin-bottom: -10px;
}

hr {
    width: 90%;
    background-color: rgb(60, 60, 60);
}

progress {
    width: 100%;
}

#btn-translate {
    width: min-content;
    padding: 1em 2em 1em 2em;
    margin-top: 1em;
    margin-bottom: 1em;
    align-self: center;
}

.form-check {
    border-style: solid;
    border-radius: 10px;
}

#btn-div {
    background-color: #c5c5c5;
    padding-top: 10px;
    padding-bottom: 10px;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
#link-previous {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #c02a5c;
    height: 1.7em;
    border-radius: 5px;
    margin-left: 2em;
    padding-right: 5px;
}

#div-previous {
    margin-bottom: 3px;
    margin-left: 5px;
    color: whitesmoke;
}

#div-next {
    margin-bottom: 3px;
    margin-right: 5px;
    color: whitesmoke;
}

#link-next {
    display: flex;
    flex-direction: row;
    align-items: center;
    background-color: #c02a5c;
    height: 1.7em;
    border-radius: 5px;
    margin-right: 2em;
    padding-left: 5px;
}

#bottom-side {
    background-color: #c5c5c5;
    height: 20vh;
    padding: 0px 0px 10px 0px;
    align-items: center; 
}

#circle-grid {
    margin: 10px 5% 0px 5%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.grid-element { 
    border-radius: 50%;
    width: 34px;
    height: 34px;
    padding: 10px;
    margin: 2px;
    border: 1px solid #000;
    color: #000;
    background-color: whitesmoke;
    text-align: center;
    font: 11px Arial, sans-serif;
}

@media (max-width: 1024px) {
    .img-answer {
        width: 80px;
        height: auto;
    }
}

@media (max-width: 768px) {
    #exam-title {
        width: 150px;
    }
    #upper-side {
        height: 12vh;
    }
    #upper-right-side {
        margin-right: 2em; 
    } 
    .top-right-element {
        margin-left: 2em;
    }
    #main-body-content {
        display: flex;
        flex-direction: row;
        background-color: lightgrey;
        height: auto;
        width: 100%;
    }
    #question-img {
        width: 80%;
    }
    #bottom-side {
        height: auto;
        display: flex;
        flex-direction: row;
        padding: 10px 0px 10px 0px;
        align-items: center; 
    }
}

@media (max-width: 600px) {
    #exam-title {
        width: 0px;
        height: 0px;
        visibility: hidden;
    }
    #main-body-content {
        flex-direction: column;
        height: auto;
    }
    #question-side {
        width: 100%;
    }
    #answer-side {
        width: 100%;
    }
}

@media (max-width: 425px) {
    #exam-title {
        width: 0px;
        height: 0px;
        visibility: hidden;
    }
    #img-pp {
        margin: 10px;
        width: 75px;
    }
    #personal-info {
        text-align: start;
        line-height: 1.2em;
        border-width: 2px;
        width: 300px;
        margin-left: 1em;
    }
    #upper-right-side {
        margin-right: 1em; 
    } 
    .top-right-element {
        margin-left: 1em;
    }
    #bottom-side {
        background-color: #c5c5c5;
        height: auto;
        display: flex;
        flex-direction: row;
        padding: 10px 0px 10px 0px;
        align-items: center; 
    }
}

@media (max-width: 375px) {
    #img-pp {
        width: 0px;
        height: 0px;
        visibility: hidden;
    }
    #personal-info {
        margin: auto; 
        line-height: 2em;
    }
    #upper-right-side {
        margin-right: 0.5em; 
    }  
    .top-right-element {
        margin-left: 0.5em;
    }  
}

@media (max-width: 320px) {
    #img-pp {
        width: 0px;
        height: 0px;
        visibility: hidden;
    }
    #personal-info {
        margin: auto;  
        line-height: 2em;      
    }
    .grid-element { 
        padding: 10px;
        margin: 1px;
        font: 11px Arial, sans-serif;
    }
    #upper-right-side {
        display: flex;
        margin-right: 0px;
        flex-direction: column-reverse;
        justify-content: center;
        align-items: center;
        width: 40%;
    }
}